/* ==================================
	Font
/* =================================*/
@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@200..800&display=swap");
/* ==================================
	Common styles
/* =================================*/
:root {
  --manrope: "Manrope";
  --bg-dark: #0D0608;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline: none;
  text-decoration: none;
  transition: 0.3s all ease;
}

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

html,
body {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  font-family: var(--manrope);
  font-weight: 400;
  letter-spacing: normal;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  background-color: var(--bg-dark);
}

a,
button {
  cursor: pointer;
}

.bg-dark {
  background-color: var(--bg-dark);
}

.wrapper {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
}

.bs-btn {
  padding: 1.12px;
  border-radius: 48px;
  background: linear-gradient(180deg, #FFC0DE 0%, #F0518A 100%);
  display: inline-block;
  width: fit-content;
}
.bs-btn .bg {
  background: linear-gradient(352deg, #EE276E 50.6%, #FFC0DE 217.46%);
  display: flex;
  align-items: center;
  column-gap: 5px;
  padding: 10px 20px;
  border-radius: 48px;
  box-shadow: 0px 2px 25px 0px rgba(255, 39, 108, 0.25), 0px 2px 55px 0px rgba(255, 39, 108, 0.25);
}
.bs-btn .bg span {
  color: #FFF;
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
  width: fit-content;
}

.title-block {
  text-align: center;
  margin-bottom: 48px;
}
.title-block .btn-txt {
  width: fit-content;
  margin: 0 auto;
  padding: 4px 16px;
  border-radius: 48px;
  border: 1px solid #282828;
  margin-bottom: 12px;
}
.title-block .btn-txt span {
  font-size: 16px;
  font-weight: 600;
  line-height: 26px;
}
.title-block h1 {
  display: inline-block;
  background: linear-gradient(89deg, rgba(0, 0, 0, 0) 29.17%, rgba(0, 0, 0, 0.55) 100%), #FFF;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 54px;
  font-weight: 600;
  line-height: 60px;
  letter-spacing: -2px;
}
.title-block h4 {
  color: #5E697F;
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
}

.liner-txt-400 {
  position: relative;
  display: inline;
  background: linear-gradient(89deg, rgba(0, 0, 0, 0) 29.17%, rgba(0, 0, 0, 0.55) 100%), #FFF;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: white;
}

.eff-txt {
  position: relative;
  padding: 0 20px;
  /* Pink gradient */
  background: radial-gradient(31.55% 121.43% at 72.59% 121.56%, #FFF 0%, rgba(255, 255, 255, 0) 100%), linear-gradient(0deg, #FF276C 0%, #FF276C 100%), linear-gradient(0deg, #FFF 0%, #FFF 100%), linear-gradient(89deg, rgba(0, 0, 0, 0) 29.17%, rgba(0, 0, 0, 0.55) 100%), #FFF;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: #ffffff;
}
.eff-txt::before {
  max-height: 97.368px;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: calc(50% + 5px);
  left: 50%;
  transform: translate(-50%, -50%);
  border: 2.351px solid #FF276C;
}
.eff-txt .detal {
  position: absolute;
  width: 8.342px;
  height: 8.342px;
  flex-shrink: 0;
  background-color: white;
  border: 2px solid rgb(255, 39, 108);
}
.eff-txt .detal:first-child {
  top: 19.5px;
  left: -5px;
}
.eff-txt .detal:nth-child(2) {
  bottom: 9.5px;
  left: -5px;
}
.eff-txt .detal:nth-child(3) {
  bottom: 9.5px;
  right: -5px;
}
.eff-txt .detal:last-child {
  top: 19.5px;
  right: -5px;
}

/* ====================
	Header
=====================*/
header {
  padding: 30px 0;
}

.header-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav-links {
  display: flex;
  align-items: center;
  column-gap: 40px;
}

.nav-links__item a {
  color: #FFF;
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: -0.42px;
}

/*=======================
	Home start
======================*/
.btn-title {
  background: linear-gradient(180deg, rgba(76, 76, 76, 0.25) 0, rgba(175, 175, 175, 0) 100%);
  width: fit-content;
  margin: 0 auto;
  background-blend-mode: overlay;
  border-radius: 100px;
  padding: 1.12px;
  padding-bottom: 0;
}
.btn-title h4 {
  z-index: 5;
  color: #ffffff;
  font-size: 16px;
  font-weight: 600;
  line-height: 26px;
  letter-spacing: -0.32px;
  padding: 6px 14px;
  border-radius: 100px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.4) 0, rgba(175, 175, 175, 0) 100%);
}

.home-wrapper {
  padding: 120px 0 94px 0;
  text-align: center;
  color: #FFF;
}
.home-wrapper h1 {
  font-size: 100px;
  font-weight: 600;
  line-height: 120px;
  letter-spacing: -5px;
}
.home-wrapper h3 {
  color: #3D3D3D;
  font-size: 22px;
  font-weight: 400;
  line-height: 36px;
}

.home-btns {
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 24px;
  margin-top: 20px;
}
.home-btns .home-btn:last-child {
  padding: 16px 38px;
  border-radius: 48px;
  border: 2px solid #3D3D3D;
  color: #3D3D3D;
  font-size: 24px;
  font-weight: 400;
  line-height: 38px;
}
.home-btns .home-btn:last-child:hover {
  border-color: white;
  color: white;
}

.home-btn {
  padding: 2px;
}
.home-btn .bg {
  height: 74px;
  padding: 0 38px;
}
.home-btn .bg span {
  font-size: 24px;
  line-height: 38px;
}

/*=========================
	User
=========================*/
#user {
  padding: 120px 0;
}

.user-wrap {
  width: 100%;
  max-width: 822px;
  margin: 0 auto;
  position: relative;
}

.user-card {
  width: 100%;
  width: 400px;
  margin: 0 auto;
  border-radius: 44px;
  background: linear-gradient(163deg, rgba(255, 255, 255, 0.07) -22.03%, rgba(255, 255, 255, 0) 137.87%);
  backdrop-filter: blur(17.5px);
  padding: 28px;
  position: relative;
  z-index: 2;
}
.user-card .card-star {
  margin: 0 !important;
  position: absolute;
  right: calc(100% - 50px);
  top: 229px;
  margin: 0;
  display: flex;
  align-items: center;
  column-gap: 10px;
  padding: 24px;
  border-radius: 28px;
  background: linear-gradient(163deg, rgba(255, 255, 255, 0.15) -22.03%, rgba(255, 255, 255, 0) 137.87%);
  box-shadow: 0.8px 1.7px 32px 10px rgba(0, 0, 0, 0.03);
  backdrop-filter: blur(12.5px);
}
.user-card .card-star img {
  flex-shrink: 0;
}
.user-card .card-star h3 {
  color: #FFF;
  font-size: 20.625px;
  font-weight: 600;
  line-height: 27.115px;
  letter-spacing: -0.234px;
  flex-shrink: 0;
}
.user-card .card-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.user-card > *:not(:last-child) {
  margin-bottom: 20px;
}
.user-card .user-info {
  display: flex;
  align-items: center;
  column-gap: 10px;
}

.userinfo-loads {
  width: 100%;
}
.userinfo-loads span {
  display: block;
  width: 100%;
  height: 11px;
  max-width: 120px;
  border-radius: 20px;
  background: linear-gradient(92deg, rgba(244, 243, 248, 0.25) -145.56%, rgba(144, 143, 146, 0.25) 109.58%);
}
.userinfo-loads span:last-child {
  max-width: 57px;
  margin-top: 6px;
}

.user-wrap {
  max-width: fit-content;
}

.card-bg1 {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: calc(100% - 280px + 58px);
  transform: translateY(-50%);
  width: 280px;
  height: calc(100% - 90px);
  border-radius: 38px;
  background: rgba(255, 255, 255, 0.03);
  box-shadow: 0.858px 1.715px 31.728px 0px rgba(0, 0, 0, 0.04);
  backdrop-filter: blur(7.5px);
}
.card-bg1:nth-child(2) {
  left: auto;
  right: calc(100% - 280px + 58px);
}

.card-bg2 {
  width: 216px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: calc(100% - 91px);
  height: calc(100% - 188px);
  border-radius: 38px;
  background: rgba(255, 255, 255, 0.01);
  box-shadow: 0.858px 1.715px 31.728px 0px rgba(0, 0, 0, 0.04);
  backdrop-filter: blur(7.5px);
}
.card-bg2:nth-child(4) {
  left: auto;
  right: calc(100% - 91px);
}

.user-profil {
  position: absolute;
  width: 100%;
  left: calc(100% - 70px);
  bottom: 70px;
  max-width: 255.24px;
  margin: 0 !important;
  z-index: 3;
  padding: 44.39px;
  border-radius: 55.487px;
  background: linear-gradient(163deg, rgba(255, 255, 255, 0.15) -22.03%, rgba(255, 255, 255, 0) 137.87%);
  box-shadow: 1.11px 2.358px 44.39px 13.872px rgba(0, 0, 0, 0.06);
  backdrop-filter: blur(12.5px);
}
.user-profil .user-img {
  width: 100%;
  max-width: 75px;
  margin: 0 auto;
  margin-bottom: 25px;
}
.user-profil .user-about {
  text-align: center;
}
.user-profil .user-about p {
  font-size: 16.646px;
  font-weight: 500;
  line-height: 16.646px;
  letter-spacing: -0.236px;
  background: linear-gradient(92deg, rgba(244, 243, 248, 0.25) -145.56%, rgba(144, 143, 146, 0.25) 109.58%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.user-profil .user-about h3 {
  color: white;
  font-size: 27.744px;
  font-weight: 700;
  line-height: 27.744px;
  letter-spacing: -0.236px;
  margin-bottom: 5.55px;
  margin-top: 16px;
}

.user-messages h4 {
  width: fit-content;
  color: #FFF;
  font-size: 15px;
  font-weight: 600;
  line-height: 100%;
  letter-spacing: -0.17px;
  border-radius: 50px;
  background: linear-gradient(93deg, rgba(244, 243, 248, 0.1) -1.09%, rgba(144, 143, 146, 0.1) 79.54%);
  padding: 10px 20px;
  min-height: 35px;
}
.user-messages h4:first-child {
  margin-bottom: 14px;
}

.card-chart {
  width: 100%;
  height: 325.12px;
  border-radius: 40px;
  overflow: hidden;
  background: linear-gradient(163deg, rgba(0, 0, 0, 0.59) -22.03%, rgba(0, 0, 0, 0) 137.87%);
  position: relative;
}
.card-chart span.chart-line {
  display: block;
  width: 100%;
  height: 0.426px;
  background-color: #B7B8C1;
  margin: 45px 0;
}
.card-chart span.chart-line:nth-child(7) {
  width: calc(100% - 70px);
  margin-left: auto;
}
.card-chart .cursor {
  position: absolute;
  top: 95px;
  z-index: 10;
  right: 20px;
}
.card-chart .cursor::before {
  content: "";
  position: absolute;
  width: 7px;
  height: 7px;
  background-color: #FF276C;
  border: 5px solid white;
  border-radius: 50%;
  z-index: -1;
  top: -7px;
  left: -7px;
}
.card-chart .cursor h3 {
  color: #FFF;
  text-align: center;
  font-size: 23.353px;
  font-weight: 500;
  backdrop-filter: blur(18.0232143402px);
  border-radius: 25px;
  padding: 3px 30px;
  border-radius: 25.643px;
  border: 2.29px solid #FF276C;
  background: rgba(255, 39, 108, 0.18);
}

.chart-sts {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 4;
}
.chart-sts .line-clip {
  position: absolute;
  z-index: 5;
  top: -60px;
}
.chart-sts .clip-liner {
  display: block;
  transform: translateY(15px);
}

.like-emoji {
  position: absolute;
  left: 20px;
  bottom: 18px;
  width: 78px;
  height: 78px;
  transform: rotate(-8.061deg);
  flex-shrink: 0;
  border-radius: 97.5px;
  background: linear-gradient(154deg, rgba(255, 255, 255, 0.15) -162.91%, rgba(255, 255, 255, 0) 136.98%);
  backdrop-filter: blur(53.625px);
  display: flex;
  align-items: center;
  justify-content: center;
}
.like-emoji img:first-child {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0.25;
  background: lightgray 50%/cover no-repeat;
  filter: blur(12.5px);
}

/*========================
	Process
========================*/
#process {
  padding: 120px 0;
}

.process-wrapper {
  padding-top: 55px;
  display: flex;
  align-items: center;
}

.process-about {
  width: 100%;
  max-width: 50%;
}
.process-about .title-block {
  margin: 0;
  text-align: start;
}
.process-about .title-block .btn-txt {
  margin: 0;
}
.process-about .title-block .btn-txt span {
  font-size: 14px;
}
.process-about .title-block h1 {
  margin-bottom: 23px;
}

.check-list {
  display: flex;
  align-items: center;
  column-gap: 30px;
  margin: 40px 0;
}
.check-list .check {
  display: flex;
  align-items: center;
  column-gap: 10px;
}
.check-list .check h4 {
  color: #FFF;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
}

.process-about__link {
  display: flex;
  align-items: center;
  column-gap: 6px;
  color: white;
  font-size: 16px;
  font-weight: 600;
  line-height: 100%;
  letter-spacing: -0.2px;
}
.process-about__link img {
  margin-top: 3px;
}

.process-socials {
  width: 100%;
  max-width: 50%;
}

.social-line {
  margin-left: auto;
  border-radius: 100px;
  background: linear-gradient(300deg, #FFF -206.93%, rgba(255, 255, 255, 0) 115.02%);
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.06);
  padding: 1.5px;
  position: relative;
}
.social-line .bg-dark {
  border-radius: 100px;
  background-color: #1E1E1E;
}
.social-line .social-wrap {
  display: flex;
  align-items: center;
  column-gap: 10px;
  width: 100%;
  height: 100%;
  border-radius: 100px;
  padding: 10px;
  padding-right: 28px;
  background: linear-gradient(89deg, rgba(255, 255, 255, 0.0901960784) -61.11%, rgba(255, 255, 255, 0) 98.97%);
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.06);
}
.social-line:not(:last-child) {
  margin-bottom: 18px;
}
.social-line .count-num {
  position: absolute;
  z-index: 4;
  border-radius: 100px;
  background: linear-gradient(300deg, #FFF -206.93%, rgba(255, 255, 255, 0) 115.02%);
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.06);
  padding: 1px;
  overflow: hidden;
}
.social-line .count-num .bg-dark {
  border-radius: 100px;
  background-color: #1E1E1E;
}
.social-line .count-num .count-wrap {
  padding: 7px 27px;
  background: linear-gradient(89deg, rgba(255, 255, 255, 0.09) -61.11%, rgba(255, 255, 255, 0) 98.97%);
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.06);
}
.social-line .count-num span {
  color: white;
  font-size: 16.804px;
  font-weight: 600;
  line-height: 16.804px;
  letter-spacing: -0.84px;
}
.social-line .count-num--yt {
  transform: rotate(-1.645deg);
  top: -19px;
  left: 81px;
}
.social-line .count-num--fb {
  transform: rotate(4.508deg);
  top: -19px;
  right: 50px;
}
.social-line .count-num--insta {
  transform: rotate(-1.645deg);
  top: -19px;
  right: 30px;
}
.social-line .count-num--tg {
  transform: rotate(-0.19deg);
  bottom: -19px;
  left: 180px;
}

.social-line--yt,
.social-line--fb {
  max-width: 537px;
}

.social-icon {
  border-radius: 100%;
  width: 55px;
  height: 55px;
  flex-shrink: 0;
  position: relative;
}
.social-icon img {
  border-radius: 100%;
}
.social-icon .top {
  position: absolute;
  top: -15px;
  right: -15px;
  width: 30px;
  height: 30px;
  border: 1px solid #121212;
  border-radius: 50%;
  background-color: #F4D86D;
  display: flex;
  align-items: center;
  justify-content: center;
}

.load-line {
  width: 100%;
  height: 8px;
  display: flex;
  align-items: flex-start;
  background-color: white;
  border-radius: 50px;
  overflow: hidden;
}
.load-line span {
  width: 0;
  height: 100%;
  border-radius: 50px;
}

.load-line.active span {
  width: 100%;
  animation: loading 3s ease-out;
}

.load-line__isnta span {
  max-width: 79.9196787149%;
  background: #FF34D4;
  box-shadow: 0px 0px 15px 0px rgba(255, 52, 212, 0.25);
}

.load-line__yt span {
  max-width: 60%;
  background: #F00;
  box-shadow: 0px 0px 15px 0px rgba(255, 0, 0, 0.25);
}

.load-line__fb span {
  max-width: 50%;
  background: #0B83EE;
  box-shadow: 0px 0px 15px 0px rgba(11, 131, 238, 0.25);
}

.load-line__tg span {
  max-width: 39.9979919679%;
  background: #289ED9;
  box-shadow: 0px 0px 15px 0px rgba(40, 158, 217, 0.25);
}

@keyframes loading {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
/*==========================
  Networks
==========================*/
.networks {
  width: fit-content;
  max-width: 100%;
  margin: 100px auto;
  position: relative;
}
.networks .networks-slide {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 10px;
}
.networks .networks-bg {
  width: 100%;
  position: absolute;
  opacity: 0.35;
  mix-blend-mode: screen;
  filter: blur(50px);
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.networks .networks-bg div {
  display: flex;
  align-items: center;
  justify-content: center;
}
.networks::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 25%;
  height: 340px;
  background: linear-gradient(90deg, #0D0608 34.79%, rgba(13, 6, 8, 0) 100%);
  z-index: 3;
}
.networks::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  width: 25%;
  height: 340px;
  background: linear-gradient(260deg, #0D0608 34.79%, rgba(13, 6, 8, 0) 100%);
  z-index: 3;
}

/*==========================
	Advantages
==========================*/
#advantages {
  padding: 120px 0;
}

.advantage-blocks {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}
.advantage-blocks .block {
  width: 100%;
  max-width: calc(50% - 2px);
  padding: 1px;
}
.advantage-blocks .block:first-child {
  background: linear-gradient(308deg, rgba(94, 105, 127, 0.15) -9.26%, rgba(94, 105, 127, 0) 35.12%);
}
.advantage-blocks .block:nth-child(2) {
  background: linear-gradient(51deg, rgba(94, 105, 127, 0.15) -9.34%, rgba(94, 105, 127, 0) 35.39%);
}
.advantage-blocks .block:nth-child(3) {
  background: linear-gradient(233deg, rgba(94, 105, 127, 0.15) 0%, rgba(94, 105, 127, 0) 31.98%);
}
.advantage-blocks .block:last-child {
  background: linear-gradient(130deg, rgba(94, 105, 127, 0.15) 0%, rgba(94, 105, 127, 0) 34.43%);
}
.advantage-blocks .block-wrap {
  display: flex;
  align-items: flex-start;
  padding: 32px 64px;
  column-gap: 24px;
  background-color: var(--bg-dark);
  width: 100%;
  height: 100%;
}
.advantage-blocks .block-wrap:hover {
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.05) -66.4%, rgba(255, 255, 255, 0) 58.6%);
}

.block-icon {
  display: flex;
  width: 64px;
  height: 64px;
  padding: 16px;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  border-radius: 16px;
  background: #FFF;
}

.block-about h2 {
  color: #FFF;
  font-size: 20px;
  font-weight: 600;
  line-height: 100%;
  display: -webkit-box;
  max-width: 100%;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.block-about p {
  color: #5E697F;
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  margin: 11px 0;
  display: -webkit-box;
  max-width: 100%;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.block-about .chip {
  border-radius: 48px;
  background: #E6F0FF;
  padding: 2px 10px;
  font-size: 12px;
  font-weight: 600;
  line-height: 20px;
}
.block-about .chip-primary {
  color: #0B5CD5;
}
.block-about .chip-D6850B {
  background: #FFF5E6;
  color: #D6850B;
}
.block-about .chip-038F65 {
