  background-color: #E9FFE3;
}

/*=========================
	Blog
=========================*/
#blog {
  padding: 120px 0;
}

.blog-cards {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 48px;
  margin-top: 50px;
}
.blog-cards .card {
  width: 100%;
  background: linear-gradient(300deg, #FFF -206.93%, rgba(255, 255, 255, 0) 115.02%);
  box-shadow: 0px 0px 16.912px 0px rgba(0, 0, 0, 0.06);
  padding: 1px;
  border-radius: 54.119px;
}
.blog-cards .card .bg-dark {
  border-radius: 54.119px;
}
.blog-cards .col-12 {
  width: 100%;
  max-width: 100%;
}
.blog-cards .col-6 {
  width: 100%;
  max-width: calc(50% - 24px);
}

.card-wrap {
  border-radius: 54.119px;
  background: linear-gradient(89deg, rgba(255, 255, 255, 0.09) -61.11%, rgba(255, 255, 255, 0) 98.97%);
  box-shadow: 0px 0px 16.912px 0px rgba(0, 0, 0, 0.06);
}

.card-img {
  width: 100%;
  max-height: 600px;
  border-radius: 54.119px;
  background: linear-gradient(300deg, #FFF -206.93%, rgba(255, 255, 255, 0) 115.02%);
  box-shadow: 0px 0px 16.912px 0px rgba(0, 0, 0, 0.06);
  padding-bottom: 0.5px;
}
.card-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  border-radius: inherit;
}

.card-about {
  display: block;
  padding: 20px 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 20px;
  color: #FFF;
}
.card-about h1,
.card-about h4 {
  display: -webkit-box;
  max-width: 100%;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.card-about h1 {
  font-size: 33.486px;
  font-weight: 600;
  line-height: 37.207px;
  letter-spacing: -1.24px;
}
.card-about h4 {
  color: #5E697F;
  font-size: 15.221px;
  font-weight: 400;
  line-height: 25.368px;
}
.card-about .blog-btn {
  flex-shrink: 0;
}

